




























import Vue from "vue";
import Component from "vue-class-component";

import ContextMenu from "@/core/components/contextmenu.vue";

import { EventBus } from "@/game/event-bus";
import { layerManager } from "@/game/layers/manager";
import { Shape } from "@/game/shapes/shape";
import { gameStore } from "@/game/store";
import { initiativeStore } from "../initiative/store";

@Component({
    components: {
        ContextMenu,
    },
})
export default class ShapeContext extends Vue {
    visible = false;
    x = 0;
    y = 0;
    shape: Shape | null = null;
    get activeLayer(): string {
        const layer = layerManager.getLayer();
        return layer === undefined ? "" : layer.name;
    }
    open(event: MouseEvent, shape: Shape) {
        this.visible = true;
        this.x = event.pageX;
        this.y = event.pageY;
        this.shape = shape;
        this.$nextTick(() => (<HTMLElement>this.$children[0].$el).focus());
    }
    close() {
        this.visible = false;
        this.shape = null;
    }
    getLayers() {
        return layerManager.layers.filter(l => l.selectable && (gameStore.IS_DM || l.playerEditable));
    }
    getActiveLayer() {
        return layerManager.getLayer();
    }
    getInitiativeWord() {
        if (this.shape === null) return "";
        return initiativeStore.contains(this.shape.uuid) ? "Show" : "Add";
    }
    setLayer(newLayer: string) {
        if (this.shape === null) return;
        this.shape.moveLayer(newLayer, true);
        this.close();
    }
    moveToBack() {
        if (this.shape === null) return;
        const layer = this.getActiveLayer()!;
        layer.moveShapeOrder(this.shape, 0, true);
        this.close();
    }
    moveToFront() {
        if (this.shape === null) return;
        const layer = this.getActiveLayer()!;
        layer.moveShapeOrder(this.shape, layer.shapes.length - 1, true);
        this.close();
    }
    addInitiative() {
        if (this.shape === null) return;
        this.$parent.$parent.$parent;
        if (!initiativeStore.contains(this.shape.uuid)) initiativeStore.addInitiative(this.shape.getInitiativeRepr());
        EventBus.$emit("Initiative.Show");
        this.close();
    }
    openEditDialog() {
        EventBus.$emit("EditDialog.Open", this.shape);
        this.close();
    }
}
